body {
  background: #365DD6
}
#gmap {
    padding: 0;
    margin: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100vw;
  }
  .dealList{
    width: 50%;
    height: 80%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;

  }

  .resultsContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100vw;
    height:100%;
  }
  .dealCard {
    width: 90%;
    height: 60%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  
  .link-to-map {
    text-decoration: none;
    color: black;
    -webkit-transition: background-color 0.5s ease-in-out;
    -o-transition: background-color 0.5s ease-in-out;
    transition: background-color 0.5s ease-in-out;
    -webkit-transition: color 0.5s ease-in-out;
    -o-transition: color 0.5s ease-in-out;
    transition: color 0.5s ease-in-out;
    -webkit-transition: border 0.5s ease-in-out;
    -o-transition: border 0.5s ease-in-out;
    transition: border 0.5s ease-in-out;
  }
  .link-to-map:hover {
    color: white;
    background: #365DD6;
    border: 2px solid black;
    
  }
  .infowindow-link {
    text-decoration: none;
    color:#365DD6
  }

  .infobar {
    display: -ms-flexbox;
    display: flex;
    min-height: 80px;
    width: 90%;
    z-index: 4;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    background: white;
    position: fixed;
    bottom: 75px;
    margin: auto 15px;
    left: 15px;
    right: 15px;
  }
.infobarContent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
}
.infobarContent ul {
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 0;
  margin: 1px;
}
.infobarContent li {
  margin: 2px;
}
  .infoHeaders {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 100%;
    width: 50%;
    font-size: 20px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  .infobarDetails {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  .map-toggle {
    display: none;
  }

  @media screen and (max-width: 768px) {
   
    #deal-list {
      width: 100%;
      background: ghostwhite;
    }
    .dealCard {
      margin-bottom: 20px;
    }
   
    .map-toggle {
      display: -ms-flexbox;
      display: flex;
      border-radius: 30px;
      width: 80px;
      height: 30px;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
      border: 2px solid #365DD6;
      background: white;
      color: #365DD6;
    }
    .map-toggle:focus {
      outline: none;
      
    }
    .map-toggle:hover {
      border-color: white;
      background: #365DD6;
      color: white;
    }
  }


